import Image from "next/image"

const workItems = [
  {
    image: "/s1.jpg",
    title: "Precision Haircut",
    category: "Hair Styling",
  },
  {
    image: "/s2.jpg",
    title: "Color Transformation",
    category: "Color Services",
  },
  {
    image: "/s3.jpg",
    title: "Deep Conditioning Treatment",
    category: "Hair Care",
  },
  {
    image: "/s4.jpg",
    title: "Beard Styling",
    category: "Grooming",
  },
  {
    image: "/s5.jpg",
    title: "Bridal Makeup",
    category: "Special Occasion",
  },
]

export function Work() {
  return (
    <section id="work" className="py-20 lg:py-32 bg-secondary/30">
      <div className="container mx-auto px-4 lg:px-8">
        <div className="text-center mb-16">
          <h2 className="text-4xl lg:text-5xl font-serif font-bold text-foreground mb-4">Our Work</h2>
          <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
            Explore our portfolio of stunning transformations and creative styling solutions
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5 gap-6">
          {workItems.map((item, index) => (
            <div key={index} className="group relative aspect-[3/4] overflow-hidden rounded-lg border border-border hover:border-primary/50 transition-all duration-300 hover:shadow-lg hover:shadow-primary/10">
              <Image
                src={item.image}
                alt={item.title}
                fill
                className="object-cover transition-transform duration-300 group-hover:scale-105"
                sizes="(max-width: 768px) 100vw, (max-width: 1024px) 50vw, (max-width: 1280px) 33vw, 20vw"
              />
              <div className="absolute inset-0 bg-gradient-to-t from-background/90 via-background/40 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                <div className="absolute bottom-0 left-0 right-0 p-4">
                  <h3 className="text-lg font-semibold text-foreground mb-1">{item.title}</h3>
                  <p className="text-sm text-muted-foreground">{item.category}</p>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
